<script setup lang="ts">
import { useI18n } from "vue-i18n";
import { ACol, ACheckbox, ATextField } from "@actindo/core-ui";
import type {
  IDataTypeInstance,
  ICacheEntitiesAttributeInSetCache,
} from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesStockValueConfiguration } from "./models";

defineProps<{
  dataTypeInstance: IDataTypeInstance<IDataTypesStockValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  remoteAttributes?: ICacheEntitiesAttributeInSetCache[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesStockValueConfiguration>({
  set: ({ precision, ...rest }) =>
    ({
      precision: Number(precision),
      ...rest,
    }) as IDataTypesStockValueConfiguration,
});

const { t } = useI18n();
</script>

<template>
  <ACol>
    <ATextField
      v-model.number="configuration.precision"
      :label="t('datahub-plugin.stockValue.precision')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="t('datahub-plugin.stockValue.readOnly')"
      :readonly="readonly"
    />
  </ACol>
</template>

<i18n>
{
  "en": {
